html {
  font-size: 16px;
  font-family: "droidSans", tahoma, sans-serif;
  font-weight: 400;
  color: #000000;
  box-sizing: border-box;
  height: 100vh; }

body {
  height: 100%;
  display: flex;
  flex-direction: column; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a[href] {
  text-decoration: none;
  transition: 0.3s; }

img {
  max-width: 100%;
  min-height: 100%; }

.container {
  margin: 0 auto;
  max-width: 940px; }

.header {
