    padding: 0;
    margin: 0; }

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

.header__nav {
  margin-right: 44px; }
  .header__nav li:not(:last-of-type) {
    margin-right: 40px; }
  .header__nav a {
    font-size: 14px;
    color: #000000;
    letter-spacing: 0.08em; }
    .header__nav a:hover {
      color: #474747; }
    .header__nav a:active {
      opacity: 0.5; }

.header__list-icon li:not(:last-of-type) {
  margin-right: 34px; }

.header__list-icon li:first-of-type svg {
  fill: none; }

