.header__list-icon li:last-of-type {
  margin-right: 13px; }
  .header__list-icon li:last-of-type a {
    display: block;
    position: relative;
    padding-right: 7px; }

.header__list-icon li svg {
  transition: 0.3s; }

.header__list-icon span {
  position: absolute;
  top: 0;
  right: 0;
  color: #f20505;
  font-size: 10px; }

.main {
  flex: 1;
  padding: 213px 0 138px 0;
  position: relative;
  background: no-repeat center 24px/100% 74px url("../img/lines.png"), linear-gradient(transparent 99%, #3f3f3f 99%, #3f3f3f 99.4%, transparent 99.4%), no-repeat left 117% url("../img/bg-guitar.png"); }
  .main_basket {
    padding: 237px 0 138px 0; }
  .main:after {
    display: block;
    position: absolute;
    top: -30px;
    right: 0;
    width: 819px;
    height: 285px;
    transform: rotate(180deg);
    content: '';
    background: no-repeat url("../img/bg-guitar.png"); }
  .main h1 {
    font-weight: 700;
    font-size: 22px;
    margin: 0 0 21px 0;
    letter-spacing: 0.03em; }

.main__wrapper {
  display: flex;
  justify-content: space-between; }

.main__breadcrumbs {
  display: flex;
  align-items: center;
  margin-bottom: 51px; }
  .main__breadcrumbs_basket {
    margin-bottom: 29px; }
  .main__breadcrumbs a {
    font-size: 16px;
    color: #474747;
    margin-right: 10px;
    letter-spacing: 0.04em; }
    .main__breadcrumbs a:hover {
      opacity: 0.7; }
    .main__breadcrumbs a:active {
      opacity: 0.5; }
  .main__breadcrumbs svg {
    margin-right: 10px; }

.filter {
  width: 23%; }
  .filter h2 {
    font-weight: 700;
    font-size: 18px;
    margin: 0 0 30px 0;
    letter-spacing: 0.03em; }

.filter__item {
  border-top: 1px solid #dddada;
  padding: 30px 0; }
  .filter__item h2 {
    margin: 0 0 24px 0; }

.filter__item-fields {
  display: flex;
  align-items: center; }
  .filter__item-fields div {
    width: 14px;
    height: 1px;
    background-color: #585757;
    margin: 0 13px; }
  .filter__item-fields input {
    width: 84px;
    height: 28px;
    border-radius: 2px;
    border: 1px solid #9f9e9e; }
    .filter__item-fields input::placeholder {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.6);
      text-align: center; }

.filter__item-check label {
  display: block;
  cursor: pointer;
  letter-spacing: 0.03em; }
  .filter__item-check label:not(:last-of-type) {
    margin-bottom: 12px; }
  .filter__item-check label:hover::before {
    border-color: #585757; }
  .filter__item-check label:active::before {
    border-color: #c1c1c1; }
  .filter__item-check label::before {
    display: inline-block;
    vertical-align: middle;
    width: 24px;
    height: 24px;
    content: '';
    border: 1px solid #9f9e9e;
    border-radius: 2px;
    margin-right: 17px; }

.filter__item-check input[type="checkbox"] {
  display: none; }

.filter__item-check input:checked + label::before {
  background: no-repeat center url("../img/mark.png"); }

.goods {
  width: 74.5%; }

.goods__sort {
