  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;
  letter-spacing: 0.08em; }
  .goods__sort button {
    cursor: pointer;
    padding: 0;
    border: none;
    background: transparent;
    letter-spacing: 0.07em; }

.goods__buttons-type span {
  margin-right: 14px; }

.goods__buttons-type button:first-of-type {
  margin-right: 29px; }

.goods__buttons-type button:hover {
  color: #585757; }

.goods__buttons-type button:active {
  color: #9f9e9e; }

.goods__buttons-prices button {
  width: 14px;
  height: 10px;
  background: no-repeat center url("../img/triangle.png");
  margin-right: 10px; }
  .goods__buttons-prices button:first-of-type {
    margin-right: 18px; }
  .goods__buttons-prices button:last-of-type {
    transform: rotate(180deg); }
  .goods__buttons-prices button:hover {
    opacity: 0.8; }
  .goods__buttons-prices button:active {
    opacity: 0.5; }

.goods__list ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0; }

