  .goods__pagination li:last-of-type {
    margin-left: 13px; }
    .goods__pagination li:last-of-type a {
      padding: 5px 20px; }
  .goods__pagination li.active {
    border: 1px solid #585757; }
    .goods__pagination li.active a {
